<template>
  <div class="container" style="width: 400px;">
    <div class="box">
      <h1 class="is-size-4 m-4 has-text-weight-semibold"></h1>
      <div class="columns is-centered">
        <div class="column is-11">
          <form @submit.prevent="onSubmit">
            <div class="field">
              <label class="label"></label>
              <div class="control">
                <input class="input" type="text" placeholder=" " v-model="name">
                <p class="help is-danger" v-if="errors.name">{{ errors.name }}</p>
              </div>
            </div>
            <div class="field">
              <label class="label"></label>
              <div class="control">
                <input class="input" type="email" placeholder=" " v-model="email">
                <p class="help is-danger" v-if="errors.email">{{ errors.email }}</p>
              </div>
            </div>
            <div class="field">
              <label class="label"></label>
              <div class="control">
                <input class="input" type="password" placeholder=" " v-model="password">
                <p class="help is-danger" v-if="errors.password">{{ errors.password }}</p>
              </div>
            </div>
            <div class="field">
              <label class="label"> </label>
              <div class="control">
                <input class="input" type="password" placeholder=" " v-model="confirmPassword">
                <p class="help is-danger" v-if="passwordMismatch">  .</p>
              </div>
            </div>
            <div class="field pt-2">
              <div class="control">
                <button class="button is-danger has-text-weight-semibold" type="submit">
                  
                </button>
                <router-link to="/" v-slot="{ navigate }">
                  <button class="button is-light ml-3 has-text-weight-semibold" @click="navigate">
                    
                  </button>
                </router-link>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { auth, db } from '@/firebase';
import { createUserWithEmailAndPassword, updateProfile } from 'firebase/auth';
import { doc, setDoc } from 'firebase/firestore';

export default {
  name: "CreateAccountView",
  data() {
    return {
      name: '',
      email: '',
      password: '',
      confirmPassword: '',
      errors: {
        name: '',
        email: '',
        password: ''
      }
    };
  },
  computed: {
    passwordMismatch() {
      return this.password !== this.confirmPassword;
    }
  },
  watch: {
    name() {
      if (!this.name) {
        this.errors.name = " .";
      } else {
        this.errors.name = '';
      }
    },
    email() {
      const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!re.test(this.email)) {
        this.errors.email = '   .';
      } else {
        this.errors.email = '';
      }
    },
    password() {
      if (this.password.length < 6 || this.password.length > 20) {
        this.errors.password = ' 6  20   .';
      } else {
        this.errors.password = '';
      }
    },
    confirmPassword() {
      if (this.confirmPassword && this.password !== this.confirmPassword) {
        this.errors.confirmPassword = '  .';
      } else {
        this.errors.confirmPassword = '';
      }
    }
  },
  methods: {
    async onSubmit() {
      const errorMessages = Object.values(this.errors).filter(error => error);
      const email = this.email;
      const password = this.password;
      const name = this.name;

      if (errorMessages.length > 0 || this.passwordMismatch) {
        alert("   : \n" + errorMessages.join("\n"));
      } else {
        try {
          const userCredential = await createUserWithEmailAndPassword(auth, email, password);
          const user = userCredential.user;

          // Firebase Auth    
          await updateProfile(user, { displayName: name });

          // Firestore   
          await setDoc(doc(db, 'users', user.uid), {
            uid: user.uid,
            email: user.email,
            name: name
          });

          alert(" !");
          console.log("User created with name: ", this.name);
          this.$router.push('/'); //  
        } catch (error) {
          console.error("Error signing up or updating profile: ", error);
          if (error.code === 'auth/email-already-in-use') {
            alert("   .    .");
          } else {
            alert(" : " + error.message);
          }
        }
      }
    }
  }
};
</script>
<style>
.user-list{
    overflow-y: auto; 
    
    height:200px; 
  }
</style>