<template>
  <form class="login" @submit.prevent="login(email, password)">
      <div class="field">
          <label class="label"></label>
          <div class="control">
              <input class="input" type="email" placeholder=" " v-model="email" />
          </div>
      </div>

      <div class="field">
          <label class="label"></label>
          <div class="control">
              <input class="input" type="password" placeholder=" " v-model="password" />
          </div>
      </div>

      <button type="submit" class="button is-danger">
          <p class="has-text-weight-semibold"></p>
      </button>
      <router-link to="/createAccount" v-slot="{ navigate }">
          <button class="button is-light ml-3" @click="navigate">
              <p class="has-text-weight-semibold"></p>
          </button>
      </router-link>
  </form>
</template>

<script>
import { auth } from '@/firebase'; 
import { signInWithEmailAndPassword} from 'firebase/auth';

export default {
  name: 'LoginBox',
  data() {
      return {
          email: '',
          password: ''
      };
  },
  methods: {
      login(email, password) {
          signInWithEmailAndPassword(auth, email, password)
              .then((userCredential) => {
                  console.log("Login successful", userCredential.user);
                  
              })
              .catch((error) => {
                  console.error("Login failed", error);
                  alert("  ");
              });
      }
  }
}
</script>
